.catalog {
    width: 100%;
}

.catalog__container {
    max-width: 1250px;
    margin: 0 auto;
    margin-bottom: 120px;
    padding: 0 60px;

    h2 {
        width: max-content;
        margin: 0 auto;
        margin-bottom: 49px;
        padding-top: 85px;
        font-weight: 600;
        font-size: 28px;
        line-height: 42px;

        @media (max-width: $tablet-width) {
            font-size: 26px;
            line-height: 39px;
            padding-top: 69px;
            margin-bottom: 51px;
        }

        @media (max-width: $mobile-width) {
            font-size: 24px;
            line-height: 36px;
        }
    }

    @media (max-width: $tablet-width) {
        margin-bottom: 100px;
    }

    @media (max-width: $mobile-width) {
        padding: 0 25px;
    }
}

.catalog__list {
    padding: 0;
    margin: 0;
    list-style-type: none;

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 30px;
    row-gap: 30px;

    @media (max-width: $tablet-width) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 4.4%;
    }

    @media (max-width: $mobile-width) {
        grid-template-columns: 1fr;
        max-width: 270px;
        margin: 0 auto;
    }
}

.catalog__description {
    display: none;
    font-size: 14px;
    line-height: 20px;

    dt {
        font-weight: 500;
        color: $alice-blue;
        margin: 0;
        margin-bottom: 2px;
    }

    dd {
        color: $quartz;
        margin: 0;
        margin-bottom: 10px;
    }
}

.catalog__item {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 53px 258px 75px;
    max-width: 260px;

    @media (max-width: $tablet-width) {
        max-width: 309px;
    }

    h3 {
        margin: 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;
        color: $alice-blue;
        padding-top: 25px;
        padding-left: 30px;

        grid-column: 1 / 2;
        grid-row: 3 / 4;

        @media (max-width: $tablet-width) {
            padding-top: 21px;
            padding-left: 35px;
        }
    }


    &:hover,
    &:focus,
    &:focus-within {
        background-color: $tangaroa;

        .catalog__image {
            display: none;
        }

        .catalog__description {
            display: block;
            grid-column: 1 / 2;
            grid-row: 2 / 3;
            padding: 0 25px;
            padding-top: 16px;
            padding-bottom: 25px;
            overflow-y: scroll;
            scrollbar-width: none;

            /* stylelint-disable */
            &::-webkit-scrollbar {
                display: none;
            }

            /* stylelint-enable */

            @media (max-width: $tablet-width) {
                padding-left: 35px;
            }
        }

        h3 {
            grid-row: 1 / 2;
            padding-top: 29px;
            padding-left: 25px;

            @media (max-width: $tablet-width) {
                padding-left: 35px;
            }
        }

        .catalog__button {
            grid-column: 1 / 2;
            grid-row: 3 / 4;
            display: block;
        }
    }
}

.catalog__image {
    grid-column: 1 / 2;
    grid-row: 1 / -1;

    img {
        height: 100%;
        width: 100%;
    }
}

.catalog__button {
    color: $tangaroa;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    background-color: $alice-blue;
    max-width: 100%;
    padding: 10px 0;
    padding-bottom: 9px;
    margin: 0 25px;
    margin-bottom: 35px;
    text-align: center;
    user-select: none;
    display: none;

    @media (max-width: $tablet-width) {
        margin: 35px;
        margin-top: 0;
    }

    &:focus,
    &:hover {
        background-color: $quartz;
        color: $prussian-blue;
        opacity: 0.8;
    }

    &:active {
        opacity: 1;
    }
}