.contacts__container {
    max-width: 1250px;
    margin: 0 auto;
    padding: 120px 45px;

    display: flex;
    gap: 8%;

    @media (max-width: $tablet-width) {
        padding: 100px 45px;
        gap: 4%;
    }

    @media (max-width: $mobile-width) {
        flex-direction: column;
        padding: 65px 15px;
    }
}

.contacts__map {
    flex-shrink: 0;

    img {
        @media (max-width: $tablet-width) {
            width: 324px;
            height: 335px;
        }

        @media (max-width: $mobile-width) {
            max-width: 290px;
            max-height: 316px;

        }
    }
}

.contacts__description {
    padding-top: 74px;
    padding-left: 7px;

    @media (max-width: $tablet-width) {
        padding-top: 65px;
        padding-left: 3px;
    }

    @media (max-width: $mobile-width) {
        order: -1;
    }

    h2 {
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;

        margin: 0;
        margin-bottom: 32px;

        @media (max-width: $tablet-width) {
            margin-bottom: 28px;
        }
    }
}

.contacts__link {
    display: flex;
    align-items: center;
    margin-bottom: 21px;

    font-size: 16px;
    line-height: 24px;
    color: $tangaroa;

    @media (max-width: $mobile-width) {
        margin-bottom: 33px;
    }

    svg {
        margin-right: 10px;
    }

    p {
        margin: 0;

        @media (max-width: $tablet-width) {
            max-width: 255px;
        }
    }

    &:focus,
    &:hover {
        color: $prussian-blue;
        opacity: 0.7;
    }

    &:active {
        opacity: 1;
    }
}

.contacts__icon {

    &--map-pin {
        @media (max-width: $tablet-width) {
            transform: translateY(-8px);
        }
    }
}